.calendly-inline-widget{
    height: calc(100vh - 70px);
}
.ugc-calendly-form::before{
    content: '';
    background-image: url(../../../../public/assets/images/ugc-bg.png);
    background-size: cover;
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
    position: absolute;
    filter: blur(2px);
}
.ugc-calendly-form .walls_tabs{
    z-index: 1;
}
.ugc-calendly-form::after{
    content: '';
    background-color: #0000001a;
    position: absolute;
    top: 0px;
    width: 100%;
    left: 0px;
    display: block;
    height: 100%;
}
.ugcform-height{
    display: flex;
    align-items: center;
    width: 100%;
}
.ugcform-height #my-hubspot-form{
    position: relative;
    width: 700px;
    margin: 0 auto;
    overflow: auto;
    border-radius: 10px;
    background-color: #fff;
    padding: 15px 30px;
}
@media (max-width: 1600px){
    .ugcform-height #my-hubspot-form{
        transform: scale(0.9);
    }
}
@media (max-width: 567px){
    .ugcform-height{
        align-items: start;
    }
    .ugcform-height #my-hubspot-form{
        width: 100%;
    }
    .ugcform-height #my-hubspot-form{
        transform: inherit;
    }
}
