}
ul.select_payment_g li {
    display: inline-block;
    vertical-align: middle;
    padding-right: 0;
    width: 48.3%;
    margin-bottom: 15px;
    position: relative;
    text-align: center;
}

ul.select_payment_g li:last-child {
    margin-left: 15px;
}

ul.select_payment_g li a img {
    max-height: 47px;
    max-width: 187px;
}

ul.select_payment_g li a {
    background-color: #fff;
    display: block;
    padding: 40px 15px;
    border-radius: 8px;
    width: 100%;
    border: 1px solid #ccc;
    min-height: 112px;
    position: relative;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

ul.select_payment_g li a span {
    position: absolute;
    width: 30px;
    height: 30px;
    display: inline-block;
    border-radius: 50%;
    right: -11px;
    top: -12px;
}

ul.select_payment_g li a:hover, ul.select_payment_g li a.selected{
    border: 1px solid #45a704;
}

ul.select_payment_g li a:hover span, ul.select_payment_g li a.selected span{
    border: 0px solid #50BCFE;
    background: #fff;
}

ul.select_payment_g li a:hover span img, ul.select_payment_g li a.selected span img {
    opacity: 1;
    transform: scale(1);
}
ul.select_payment_g li a span img {
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: all .3s;
    transform: scale(0);
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.multi_payment_g {
    background: #fff;
    width: 100%;
    max-width: 600px;
