ul.platform__icons li {
    display: inline-block;
    vertical-align: middle;
    margin: 0 3px;
    position:relative;
}
ul.platform__icons, ul.platform__icons ul {
    padding: 0;
    list-style: none;
}
ul.platform__icons li ul {
    position: absolute;
    background-color: #fff;
    padding: 8px;
    min-width: 170px;
    box-shadow: 0 0 12px rgba(0,0,0,.1);
    border-radius: 3px;
    right: -18px;
    left: auto;
    top: 110%;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
    margin: 10px auto;
    text-align: center;
    z-index:9;
}

ul.platform__icons li:hover ul.platform__more_{
    top: 100%;
    opacity:1;
    visibility: visible;
    transition: all 0.3s ease;
}

ul.platform__icons li svg,
ul.platform__icons li i {
    vertical-align: middle;
    display: block;
    line-height: 30px;
}

