.ugc-suite-section {
	box-shadow: 0 1px 2px rgb(0 0 0/6%), 0 2px 4px rgb(0 0 0/4%), 0 5px 14px rgb(0 0 0/2%);
	border-radius: 7px;
	color: #2b2b2b;
	background-color: #fff;
	margin: 0 auto;
	position: relative;
	align-items: center;
}

.ugc-suite-section .top_text {
	background-color: #e6f3fe;
	border-radius: 0 0 15px 15px;
	padding: 5px 20px;
	text-align: center;
	text-transform: uppercase;
	color: #2b2b2b;
	font-size: 12px;
	font-weight: 400;
