
.com_guid_video {
	min-height: 250px;
}

.com_guid_video img {
	width: 100%;
}

.feed_steps:after {
	content: '';
	position: absolute;
	background-color: #EDF0F1;
	height: 100%;
	width: 1px;
	right: 0;
	top: 0;
	z-index: 0;
}

.commerce_conetnt .feed_steps li.nav-item {
	display: block;
	margin: 0;
	float: none;
	position: relative;
	z-index: 1;
	margin-right: 0;
}

.commerce_conetnt .feed_steps>li>a {
	display: block;
	text-decoration: none;
	padding: 20px 20px;
	color: #333;
	background-color: #F9FBFC;
	border-right: 1px solid #EDF0F1;
	border-bottom: 1px solid #EDF0F1;
	margin: 0;
	white-space: nowrap;
	position: relative;
}

.c_step_wrap {
	border-top: 1px solid #EDF0F1;
	position: relative;
}

.step_content_ {
	padding: 20px 30px;
	position: relative;
	padding-bottom: 80px;
	min-height: 326px;
}

.step_title_ {
	padding: 10px 20px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	justify-content: space-between;
	align-items: center;
}

span.prog_perc {
	font-size: 18px;
}

.feed_steps li a span {
	width: 20px;
	height: 20px;
	border: 1px solid #333;
	display: inline-block;
	text-align: center;
	line-height: 18px;
	margin-right: 10px;
	border-radius: 50%;
	font-size: 12px;
	color: #333;
	position: relative;
}

.feed_steps li a.step_complete span:after {
	content: '';
	position: absolute;
	left: -2px;
	right: 0;
	top: -2px;
	background-image: url("data:image/png;base64,");
	background-color: #fff;
	border-radius: 50%;
	width: 22px;
	height: 22px;
	background-position: center;
	background-size: contain;
}

.commerce_conetnt .feed_steps li>a.active {
	background-color: #fff;
	border-right: 1px solid transparent;
	border-bottom: 1px solid #EDF0F1;
}

.commerce_conetnt .feed_steps li>a.active:focus,
.commerce_conetnt .feed_steps li>a.active:hover {
	background-color: #fff;
	border-right: 1px solid transparent;
	border-bottom: 1px solid #EDF0F1;
}

.ques_info {
	position: absolute;
	bottom: 30px;
	left: 30px;
}

.qus_guid {
	padding: 8px 15px;
	display: table;
	border: 1px solid #ff4051;
	border-radius: 30px;
	color: #ff4051;
	text-decoration: none;
	-webkit-transition: all .3s;
	-o-transition: all .3s;
	transition: all .3s;
}

.qus_guid:hover {
	box-shadow: 0 0 0 0.1rem #ff4051;
}

.qus_guid svg path {
	fill: #ff4051;
}

i.icons_ques {
	display: inline-block;
	margin-right: 8px;
	height: 21px;
	line-height: 18px;
	vertical-align: middle;
}

.com_vid_con {
	display: block;
	margin-bottom: 30px;
}

.com_guid_video {
	overflow: hidden;
	position: relative;
}

.com_guid_video:after {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	background-color: rgba(0, 0, 0, 0);
	width: 100%;
	height: 100%;
	z-index: 1;
	transition: .3s;
}

.feed_status__ p i {
    font-size: 14px;
    margin-right: 5px;
}

.com_guid_video:hover:after {
	background-color: rgba(0, 0, 0, .5);
}

.subheader_main .nav.nav-tabs .nav-link .nav-text {
	color: #545454;
	font-weight: 600;
}

.subheader_main .nav.nav-tabs .nav-link.active .nav-text {
	color: #2b2b2b;
}

a.pay_icon_ {
	position: absolute;
	top: 40%;
	top: calc(50% - 25px);
	left: 0;
	right: 0;
	margin: 0 auto;
	display: inline-block;
	text-align: center;
	z-index: 3;
	width: 50px;
	height: 50px;
	transition: .3s;
}

a.pay_icon_:hover {
	transform: scale(1.1);
}

a.pay_icon_ img {
	max-width: 100%;
}

.c_progress_bar {
	position: absolute;
	top: -2px;
	width: 50%;
	height: 3px;
	background-color: #00BE34;
	z-index: 9;
	transition: all 2s;
}

.com_vid_con h4 {
	margin-top: 0;
	margin-bottom: 12px;
	font-size: 16px;
}

.learn_more {
	font-size: 16px;
	color: #ff4051;
	text-decoration: none;
	transition-duration: .3s;
}

.learn_more svg {
	width: 20px;
	transition: .3s;
}

.learn_more svg path {
	fill: #ff4051;
}

.learn_more:hover svg {
	transform: translateX(5px);
}

.learn_more:hover {
	color: #ff4051;
}

.demo_btns p {
	text-align: right;
}

.demo_btns p a {
	margin-top: 12px;
}

.c_demo_post .card-label {
	margin-top: 12px;
}
.preview.display_preview .device {
    border: 12px solid #1a2029;
    border-radius: 1em;
}
@media (max-width: 991px) {
    .commerece_step {
        margin-right: 0px;
    }
}
@media (max-width: 767px) {
	.step_content_ {
		padding: 15px 20px;
		position: relative;
		padding-bottom: 70px;
		min-height: 220px;
	}
	.ques_info a {
		padding: 6px 12px;
		font-size: 12px;
	}
	i.icons_ques svg {
		width: 17px;
		vertical-align: top;
	}
	.commerce_conetnt .feed_steps>li>a {
		padding: 12px 20px;
	}
	.page_custom_header h5 {
		font-size: 16px;
	}
	table.notification-heading td:nth-child(2) {
        text-align: left;
    }
}


.wall_cont_wrap_display .overlay .overlay-layer,
.innter_wall_cont_wrap_ .overlay .overlay-layer {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
	background-color: rgb(0, 0, 0, 0.4);
	opacity: 1;
	z-index: 8;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}
.wall_cont_wrap_display .overlay-layer img,
.innter_wall_cont_wrap_ .overlay-layer img  {
	max-width: 46px;
}

.dash_walls .wall_card{
    border-radius: 4px;
}

.wall_cont_wrap_display a:hover .overlay-layer,
.innter_wall_cont_wrap_ a:hover .overlay-layer {
	background-color: rgb(0, 0, 0, 0.1);
    display: none;
}

.wall_cont_wrap_display video,
.wall_cont_wrap video{
    position: absolute;
    top: 0;
    left: 0;
}

.wall_cont_wrap_display a:hover .theme_media_thumb video,
.innter_wall_cont_wrap_ a:hover .theme_media_thumb video {
	visibility: visible;
	opacity: 1;
}

.wall_cont_wrap_display a:hover .theme_thumbnail,
.innter_wall_cont_wrap_ a:hover .theme_thumbnail {
	visibility: hidden;
	opacity: 0;
}



.add-feed-modal .add-feed-modal-body-inner {
    max-width: 600px;
    padding: 60px 0;
    margin: 0 auto;
}

.add-feed-modal .themesNowThumbs ul {
    text-align: left;
}

.add-feed-modal .modal-body {
    padding: 0;
}

.add-feed-modal .themesNowThumbs ul li {
    transition: 0.3s linear;
    margin-bottom: 20px;
    padding-right: 20px;
    padding-left: 20px;
}
.theme_view_grid .theme_thumb_view {
    height: auto;
    background-size: 100%;
    background-position: top center;
    background-repeat: repeat-y;
}

.theme_thumb_view img {
    opacity: 0;
    visibility: hidden;
}

.theme_view_grid .theme_thumb_view:hover {
    animation: animatedBackground 900s linear infinite;
}

.theme_view_grid .theme_view {
    height: 191px;
}

.theme_view_grid.horizontal_theme_view .theme_thumb_view {
    background-repeat: repeat-x;
    background-position: left center;
    background-size: 200%;
}

.theme_view_grid.horizontal_theme_view .theme_thumb_view:hover {
    animation: animatedBackgroundHorizontal 900s linear infinite;
}

.wall_head .theme_media_thumb:hover .theme_thumbnail {
    visibility: hidden;
    opacity: 0;
    transition: all .3s;
}

.all_notifications__ .table-scrollable-borderless {
    overflow-x: auto;
}

.notification-heading{
    min-width: 600px;
}

.add-feed-modal .display_theme_preview .add-feed-modal-bigthumb {
    background: #f5f5f5;
    padding: 15px;
    margin-bottom: 20px;
    border-radius: 5px;
}

.add-feed-modal .themesNowThumbs ul li:hover a span.imgThumb {
    background: #f5f5f5;
    border-color: #DEDEDE;
    box-shadow: none;
}

.add-feed-modal .themesNowThumbs ul li.active {
    transform: scale(1);
    -moz-transform: scale(1);
    -webkit-transform: scale(1);
}

.add-feed-modal .themesNowThumbs .form-group.form-md-radios ul li a label {
    position: relative;
}

.add-feed-modal .add-feed-devices {
    padding-bottom: 40px;
    border-bottom: 1px solid #DEDEDE;
    margin-bottom: 20px;
}

.add-feed-modal .add-feed-devices li {
    display: inline-block;
    margin-right: 10px;
}

.add-feed-modal .add-feed-devices li:last-child {
    margin-right: 0;
}

.add-feed-modal .add-feed-devices li label {
    background-color: #ebedf3;
    color: #545454;
    padding: 6px 16px;
    border-radius: 4px;
    font-size: 12px;
}

.wall_cont_wrap a:hover .theme_view_grid.horizontal_theme_view .theme_thumb_view {
    animation: animatedBackgroundHorizontal 900s linear infinite;
}

.wall_cont_wrap a:hover .theme_view_grid .theme_thumb_view {
    animation: animatedBackground 900s linear infinite;
}

.theme_view {
    background-size: 100%;
    background-position: top center;
    background-repeat: repeat-y;
    animation: animatedBackground 900s linear infinite;
}

.theme_view.horizontal_theme_view {
    background-size: 200%;
    background-repeat: repeat-x;
    background-position: left center;
    animation: animatedBackgroundHorizontal 900s linear infinite;
}

@keyframes animatedBackground {
    from {
        background-position: 0 0;
    }
    to {
        background-position: 0 -18000px;
    }
}

@keyframes animatedBackgroundHorizontal {
    from {
        background-position: 0 center;
    }
    to {
        background-position: -18000px center;
    }
}
