.tb_onsite_error{
    color:#ff4051;
    font-family:Inter,sans-serif;
    font-size:12px
}
.tb_onsite_terms_condition{
    color:#2b2b2b;
    font-family:Inter,sans-serif;
    font-size:14px;
    margin-bottom:20px;
    margin-top:0
}
.tb_onsite_terms_condition a{
    color:#2b2b2b;
    font-weight:600;
    text-decoration:none
}
.tb_onsite_btn_wrapper{
    align-items:center;
    display:flex;
    justify-content:center
}
.tb_onsite_post_limits{
    font-family:Inter,sans-serif;
    font-size:12px;
    margin-left:10px
}
.tb_onsite_form_actions_footer{
    align-items:start;
    display:flex;
    justify-content:space-between;
    padding-top:10px
}
.tb_onsite_btn_primary.tb_onsite_spinner{
    box-shadow:none;
    opacity:.9;
    padding-right:2.2rem;
    pointer-events:none;
    position:relative
}
.tb_onsite_spinner:after{
    -webkit-animation:tb_onsite_animation-spinner .5s linear infinite;
    animation:tb_onsite_animation-spinner .5s linear infinite;
    border:2px solid #fff;
    border-radius:50%;
    border-right-color:transparent;
    content:"";
    display:inline-block;
    height:22px;
    position:absolute;
    right:12px;
    top:calc(50% - 11px);
    vertical-align:middle;
    width:22px
}
@-webkit-keyframes tb_onsite_animation-spinner{
    to{
        -webkit-transform:rotate(1turn);
        transform:rotate(1turn)
    }
}
@keyframes tb_onsite_animation-spinner{
    to{
        -webkit-transform:rotate(1turn);
        transform:rotate(1turn)
    }
}
.tb_onsite_btn_disabled{
    box-shadow:none;
    opacity:.6;
    pointer-events:none
}
@media(max-width:580px){
    .tb_onsite_btn_wrapper{
        align-items:start;
        flex-direction:column;
        justify-content:start
    }
    .tb_onsite_tabs_content{
        padding:15px
    }
    .tb_onsite_btn_primary,.tb_onsite_btn_secondary{
        font-size:14px;
        padding:12px 10px
    }
    .tb_onsite_title{
        font-size:18px
    }
    .tb_onsite_checkbox,.tb_onsite_sub_heading{
        font-size:14px
    }
    .tb_onsite_form_label,.tb_onsite_terms_condition{
        font-size:12px
    }
    .uppy-DragDrop-label{
        font-size:14px
    }
    .tb_onsite_upload_box_content .uppy-DragDrop-note{
        font-size:12px
    }
    .uppy-DragDrop-inner{
        padding:60px 10px
    }
    .tb_onsite_separator{
        font-size:12px;
        height:34px;
        line-height:34px;
        width:34px
    }
    .tb_onsite_post_limits{
        font-size:10px;
        margin-left:0;
        margin-top:5px
    }
}
